<template>
  <div class="auth-wrapper auth-v1">
    <v-snackbar
          v-model="snackbar"
          bottom
          center
          :color="color"
          >
          {{msg}}
          <v-btn
          color="black"
          text
          @click="snackbar = false"
          >
          
          </v-btn>

          </v-snackbar>
    <div class="auth-inner">
      <v-card class="auth-card">
        <!-- logo -->
        <v-card-title class="d-flex align-center justify-center py-7">
          <router-link
            to="/"
            class="d-flex align-center"
          >
            <v-img
              :src="require('@/assets/images/logos/logo.png')"
              max-height="30px"
              max-width="30px"
              alt="logo"
              contain
              class="me-3 "
            ></v-img>

            <h2 class="text-2xl text-center font-weight-semibold">
              Linkway Service
            </h2>
          </router-link>
        </v-card-title>

        <!-- title -->
        <v-card-text>
          
          <p class="mb-2 text-center">
            Customer Service Application
          </p>
        </v-card-text>

        <!-- login form -->
        <v-card-text>
          <v-form ref="form" @submit.prevent="login_fun()">
            <v-text-field
              v-model="login_.username"
              outlined
              label="Email"
              placeholder="john@example.com"
              hide-details
              class="mb-3"
            ></v-text-field>

            <v-text-field
              v-model="login_.password"
              outlined
              :type="isPasswordVisible ? 'text' : 'password'"
              label="Password"
              placeholder="············"
              :append-icon="isPasswordVisible ? icons.mdiEyeOffOutline : icons.mdiEyeOutline"
              hide-details
              @click:append="isPasswordVisible = !isPasswordVisible"
            ></v-text-field>

            <div class="d-flex align-center justify-space-between flex-wrap">
              <v-checkbox
                label="Remember Me"
                hide-details
                class="me-3 mt-1"
              >
              </v-checkbox>

              <!-- forgot link -->
              <a
                href="javascript:void(0)"
                class="mt-1"
              >
                Forgot Password?
              </a>
            </div>

            <v-btn
              block
              color="primary"
              class="mt-6"
              type="submit"
            >
              Login
            </v-btn>
          </v-form>
        </v-card-text>

        

        <!-- divider -->
        
      </v-card>
    </div>

    <!-- background triangle shape  -->
    <img
      class="auth-mask-bg"
      height="173"
      :src="require(`@/assets/images/misc/mask-${$vuetify.theme.dark ? 'dark':'light'}.png`)"
    >

    <!-- tree -->
    <v-img
      class="auth-tree"
      width="247"
      height="185"
      src="@/assets/images/misc/tree.png"
    ></v-img>

    <!-- tree  -->
    <v-img
      class="auth-tree-3"
      width="377"
      height="289"
      src="@/assets/images/misc/tree-3.png"
    ></v-img>
  </div>
</template>

<script>
// eslint-disable-next-line object-curly-newline
import { mdiEyeOutline, mdiEyeOffOutline } from '@mdi/js'
import axios from "axios";
const CryptoJS = require("crypto-js")
export default {
  data() {
    return {
      snackbar:false,
        msg:'',
        color:'',
      isPasswordVisible:'',
      login_:{
        username:'',
        password:'',
      },
      icons: {
        mdiEyeOutline,
        mdiEyeOffOutline,
      },
    }
  },
  methods:{
    encrypt (val) {
    const passphrase = '123456'
    this.login_.password =  CryptoJS.AES.encrypt(val, passphrase).toString();
    return true;
   },
    login_fun()
    {
      // console.log(this.login.email)
   
        let apiURL = 'api/postApi/login';
        axios.post(apiURL, this.login_).then(res => {
          
          if(res.data.length === 0)
          {
            this.color = 'error'
            this.msg ='invalid Username or password.'
            this.snackbar = true;
          }else
          {
            this.login_ = {
              username: '',
              password: '',
            }
            localStorage.setItem("userinfo",JSON.stringify(res.data[0]))
            localStorage.setItem("Userrole",res.data[0].Userrole)
            localStorage.setItem("loggedUser",res.data[0].Name)
            this.$router.push('/services')
          }
          
          
          
        }).catch(error => {
          console.log(error)
        });
      }
    
   },
    
}
</script>

